.user {
    display: grid;
    grid-template-columns: 100px 1fr;
}

.avatar {
    width: 50px;
    height: 50px;
    border-radius: 50%;
}

.pagination {
    display: flex;
    flex-wrap: wrap;
}

.pagEl {
    margin: 0 4px;
    cursor: pointer;
}

.selected {
    font-weight: bold;
}